* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  font-family: Signika, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

h1, h2, h3 {
  color: #000;
  font-weight: 700;
}

p {
  margin-bottom: 1rem;
  font-size: 1rem;
}

a {
  color: #00f;
  text-decoration: none;
}

a:hover {
  color: #000;
}

a.more:after {
  content: " →";
  width: 3rem;
  height: 2rem;
  display: inline-block;
}

h1 {
  color: gray;
  margin-bottom: 1.5rem;
  font-size: 3.25rem;
  line-height: 1em;
}

h2 {
  font-size: 2rem;
  line-height: 1em;
}

h3 {
  margin-bottom: 1em;
  font-size: 2rem;
}

header {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #db0a13;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

main {
  padding-top: 3rem;
  padding-bottom: 4rem;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}

.logotipo {
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
}

.logotipo a {
  color: #e6545a;
}

.logotipo a:hover {
  color: #db0a13;
  border: none;
}

nav.nav {
  text-align: center;
  color: #e6545a;
}

nav.nav ul {
  margin-top: 1rem;
  list-style-type: none;
}

nav.nav ul li {
  font-size: 1.2rem;
  font-weight: 800;
}

nav.nav ul li a {
  color: inherit;
}

nav.nav ul li a:hover {
  color: #db0a13;
  border: none;
}

nav.nav a.active, nav.nav a.active:hover {
  border-bottom: 3px solid;
}

@media (min-width: 992px) {
  nav.nav {
    text-align: left;
  }

  nav.nav ul li {
    padding-right: 30px;
    display: inline-block;
  }

  nav.nav ul li:last-child {
    padding-right: 0;
  }

  header .container {
    justify-content: space-between;
    align-items: flex-end;
    display: flex;
  }
}

footer ul li {
  display: block;
}

footer a {
  color: #fff;
}

footer a:hover {
  color: #f1e1e1;
  border: none;
}

footer .firma {
  margin-top: 2rem;
}

footer .firma p {
  margin-bottom: .5em;
  font-size: .7rem;
}

@media (min-width: 768px) {
  footer ul li {
    padding-left: 1rem;
    display: inline-block;
  }

  footer ul li:first-child {
    padding-left: 0;
  }

  footer ul li:last-child {
    padding-right: 0;
  }

  .firma {
    font-size: 1.5rem;
  }
}

footer .logotipo a {
  color: #fff;
}

footer .logotipo a:hover {
  color: #f1e1e1;
}

.lista-sitios {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.lista-sitios li {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.lista-sitios li div {
  width: 100%;
}

.lista-sitios li div:first-child {
  margin-bottom: 10px;
}

.lista-sitios li span {
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: bold;
}

.lista-sitios li img {
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 10px;
}

.lista-sitios li p {
  text-align: justify;
  margin-bottom: 10px;
  font-size: 1rem;
  line-height: 1.5;
}

.lista-sitios li p a {
  color: #e6545a;
}

.lista-sitios li p a:hover {
  color: #db0a13;
}

article h1 {
  text-align: center;
}

figcaption {
  margin-bottom: 10px;
}

article p {
  text-align: justify;
}

figure, figcaption p {
  text-align: center;
}

@media (max-width: 740px) {
  figure img {
    height: 90%;
    width: 90%;
  }

  .imagen-portada {
    height: 80%;
    width: 80%;
  }

  .lista-sitios {
    text-align: center;
    align-items: center;
    display: block;
  }

  .container ul {
    font-size: .6rem;
  }
}

@media (max-width: 1200px) {
  .imagen-portada {
    height: 80%;
    width: 80%;
  }
}

.contenedor-imagen img {
  height: 80%;
  width: 80%;
  object-fit: cover;
  -webkit-clip-path: polygon(15% 0, 85% 0, 100% 15%, 100% 85%, 85% 100%, 15% 100%, 0 85%, 0 15%);
  clip-path: polygon(15% 0, 85% 0, 100% 15%, 100% 85%, 85% 100%, 15% 100%, 0 85%, 0 15%);
}

.logotipo polygon {
  fill: #db0a13;
  stroke: #db0a13;
  stroke-width: 2px;
}

.logotipo-pie polygon {
  fill: #db0a13;
  stroke: #fff;
  stroke-width: 2px;
}

.logotipo text, .logotipo-pie text {
  font-family: Signika, sans-serif;
  font-size: 2.8rem;
  font-weight: 700;
}

@keyframes shine {
  0% {
    fill: #db0a13;
    opacity: .3;
  }

  50% {
    fill: #fff;
    opacity: 1;
  }

  100% {
    fill: #db0a13;
    opacity: .3;
  }
}

#diamante {
  animation: 2s linear infinite shine;
}

.rombos {
  display: flex;
}

.rombo {
  flex: 0 0 20%;
}

.rombo svg {
  width: 100%;
  height: 100%;
}

